import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpErrorResponse,
  HttpHeaders,
  HttpResponse,
  HttpResponseBase
} from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, of, throwError, catchError, filter, mergeMap, switchMap, take, Observable } from 'rxjs';

import { YA_SERVICE_TOKEN, ITokenService, ALLOW_ANONYMOUS } from '@yelon/auth';
import { YUNZAI_I18N_TOKEN, _HttpClient } from '@yelon/theme';
import { WINDOW } from '@yelon/util';
import { YunzaiBusinessConfig, YunzaiConfigService } from '@yelon/util/config';
import { log } from '@yelon/util/other';
import { NzNotificationService } from 'ng-zorro-antd/notification';

import { mergeBisConfig } from './bis.config';

const CODEMESSAGE: { [key: number]: string } = {
  200: 'ÊúçÂä°Âô®ÊàêÂäüËøîÂõûËØ∑Ê±ÇÁöÑÊï∞ÊçÆ„ÄÇ',
  201: 'Êñ∞Âª∫Êàñ‰øÆÊîπÊï∞ÊçÆÊàêÂäü„ÄÇ',
  202: '‰∏Ä‰∏™ËØ∑Ê±ÇÂ∑≤ÁªèËøõÂÖ•ÂêéÂè∞ÊéíÈòüÔºàÂºÇÊ≠•‰ªªÂä°Ôºâ„ÄÇ',
  204: 'Âà†Èô§Êï∞ÊçÆÊàêÂäü„ÄÇ',
  400: 'ÂèëÂá∫ÁöÑËØ∑Ê±ÇÊúâÈîôËØØÔºåÊúçÂä°Âô®Ê≤°ÊúâËøõË°åÊñ∞Âª∫Êàñ‰øÆÊîπÊï∞ÊçÆÁöÑÊìç‰Ωú„ÄÇ',
  401: 'Áî®Êà∑Ê≤°ÊúâÊùÉÈôêÔºà‰ª§Áâå„ÄÅÁî®Êà∑Âêç„ÄÅÂØÜÁ†ÅÈîôËØØÔºâ„ÄÇ',
  403: 'Áî®Êà∑ÂæóÂà∞ÊéàÊùÉÔºå‰ΩÜÊòØËÆøÈóÆÊòØË¢´Á¶ÅÊ≠¢ÁöÑ„ÄÇ',
  404: 'ÂèëÂá∫ÁöÑËØ∑Ê±ÇÈíàÂØπÁöÑÊòØ‰∏çÂ≠òÂú®ÁöÑËÆ∞ÂΩïÔºåÊúçÂä°Âô®Ê≤°ÊúâËøõË°åÊìç‰Ωú„ÄÇ',
  406: 'ËØ∑Ê±ÇÁöÑÊ†ºÂºè‰∏çÂèØÂæó„ÄÇ',
  410: 'ËØ∑Ê±ÇÁöÑËµÑÊ∫êË¢´Ê∞∏‰πÖÂà†Èô§Ôºå‰∏î‰∏ç‰ºöÂÜçÂæóÂà∞ÁöÑ„ÄÇ',
  422: 'ÂΩìÂàõÂª∫‰∏Ä‰∏™ÂØπË±°Êó∂ÔºåÂèëÁîü‰∏Ä‰∏™È™åËØÅÈîôËØØ„ÄÇ',
  500: 'ÊúçÂä°Âô®ÂèëÁîüÈîôËØØÔºåËØ∑Ê£ÄÊü•ÊúçÂä°Âô®„ÄÇ',
  502: 'ÁΩëÂÖ≥ÈîôËØØ„ÄÇ',
  503: 'ÊúçÂä°‰∏çÂèØÁî®ÔºåÊúçÂä°Âô®ÊöÇÊó∂ËøáËΩΩÊàñÁª¥Êä§„ÄÇ',
  504: 'ÁΩëÂÖ≥Ë∂ÖÊó∂„ÄÇ'
};

@Injectable()
class YunzaiDefaultInterceptor implements HttpInterceptor {
  private jump = false;
  private refreshToking = false;
  private refreshToken$: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  private get notification(): NzNotificationService {
    return this.injector.get(NzNotificationService);
  }

  private get tokenSrv(): ITokenService {
    return this.injector.get(YA_SERVICE_TOKEN);
  }

  private get http(): _HttpClient {
    return this.injector.get(_HttpClient);
  }

  private get config(): YunzaiBusinessConfig {
    return mergeBisConfig(this.injector.get(YunzaiConfigService));
  }

  private goTo(url: string): void {
    setTimeout(() => this.injector.get(Router).navigateByUrl(url));
  }

  constructor(private injector: Injector) {
    if (this.config.refreshTokenType === 'auth-refresh') {
      console.error("can't use auth-refresh, please change yz.default.interceptor to default.interceptor!");
    }
  }

  private checkStatus(ev: HttpResponseBase): void {
    if ((ev.status >= 200 && ev.status < 300) || ev.status === 401) {
      return;
    }

    if (ev instanceof HttpErrorResponse && (ev.error.message || ev.error.errorMessage)) {
      if (ev.error.errorMessage) {
        this.notification.error(`ÂèëÁîü‰∫Ü‰∏Ä‰∫õÈîôËØØ `, ev.error.errorMessage);
      } else {
        this.notification.error(`ÂèëÁîü‰∫Ü‰∏Ä‰∫õÈîôËØØ `, ev.error.message);
      }
      return;
    }

    if (ev instanceof HttpResponse && ev.body.errorMessage) {
      this.notification.error(`ÂèëÁîü‰∫Ü‰∏Ä‰∫õÈîôËØØ `, ev.body.errorMessage);
      return;
    }
    const errortext = CODEMESSAGE[ev.status] || ev.statusText;
    this.notification.error(`ËØ∑Ê±ÇÈîôËØØ ${ev.status}: ${ev.url}`, errortext);
  }

  private ToLogin(): void {
    this.notification.error(`Êú™ÁôªÂΩïÊàñÁôªÂΩïÁä∂ÊÄÅÂ∑≤ËøáÊúüÔºå5ÁßíÂêéÂ∞ÜË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢„ÄÇ`, ``);
    setTimeout(() => {
      localStorage.clear();
      this.injector.get(WINDOW).location.href = `${this.config.baseUrl}/cas-proxy/app/logout`;
    }, 5000);
  }

  private reAttachToken(req: HttpRequest<any>): HttpRequest<any> {
    const token = this.tokenSrv.get()?.access_token;
    return req.clone({
      setHeaders: {
        Authorization: `Bearer ${token}`
      }
    });
  }

  private refreshTokenRequest(): Observable<any> {
    const model = this.tokenSrv.get();
    const form = new FormData();
    form.set('refresh_token', model?.refresh_token!);
    form.set('grant_type', 'refresh_token');
    form.set('scope', 'webapp');
    log('yz.default.interceptor: use the refresh token to request a new token', model?.refresh_token);
    return this.http.post(`/auth/oauth/getOrCreateToken/webapp`, form);
  }

  private tryRefreshToken(ev: HttpResponseBase, req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    // ËøûÂà∑Êñ∞TokenÁöÑËØ∑Ê±ÇÈÉΩÈîô‰∫ÜÔºåÈÇ£Â∞±ÊòØÁúüÈîô‰∫Ü
    if (['/auth/oauth/getOrCreateToken/webapp'].some(url => req.url.includes(url))) {
      this.ToLogin();
      return throwError(() => ev);
    }

    // Ê≠£Âú®Âà∑Êñ∞tokenÔºåÊâÄÊúâÂÖ∂‰ªñËØ∑Ê±ÇÊéíÈòü
    if (this.refreshToking) {
      return this.refreshToken$.pipe(
        filter(v => !!v),
        take(1),
        switchMap(() => next.handle(this.reAttachToken(req)))
      );
    }

    //Â∞ùËØïË∞ÉÁî®Âà∑Êñ∞ Token
    this.refreshToking = true;
    this.refreshToken$.next(null);

    // Â§ÑÁêÜToken
    return this.refreshTokenRequest().pipe(
      switchMap(res => {
        log('yz.default.interceptor: refresh token accessed -> ', res);
        // ÈáçÊñ∞‰øùÂ≠òÊñ∞ token
        this.tokenSrv.set(res);
        // ÈÄöÁü•ÂêéÁª≠ËØ∑Ê±ÇÁªßÁª≠ÊâßË°å
        this.refreshToking = false;
        this.refreshToken$.next(res);
        // ÈáçÊñ∞ÂèëËµ∑ËØ∑Ê±Ç
        return next.handle(this.reAttachToken(req));
      }),
      catchError(err => {
        this.refreshToking = false;
        this.ToLogin();
        return throwError(() => err);
      })
    );
  }

  private getAdditionalHeaders(headers?: HttpHeaders): { [name: string]: string } {
    const res: { [name: string]: string } = {};
    const lang = this.injector.get(YUNZAI_I18N_TOKEN).currentLang;
    if (!headers?.has('Accept-Language') && lang) {
      res['Accept-Language'] = lang;
    }
    return res;
  }

  private handleData(ev: HttpResponseBase, req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    this.checkStatus(ev);
    switch (ev.status) {
      case 200:
        return of(ev);
      case 401:
        log("http401: ",req.url)
        if (this.config.refreshTokenEnabled && this.config.refreshTokenType === 're-request') {
          const unAuthorizationReq = req.clone();
          unAuthorizationReq.headers.delete('Authorization');
          return this.tryRefreshToken(ev, unAuthorizationReq, next);
        }
        this.ToLogin();
        break;
      case 403:
      case 404:
      case 500:
        if (this.jump) {
          this.goTo(`/exception/${ev.status}`);
        }
        break;
      default:
        if (ev instanceof HttpErrorResponse) {
          console.warn(
            'Êú™ÂèØÁü•ÈîôËØØÔºåÂ§ßÈÉ®ÂàÜÊòØÁî±‰∫éÂêéÁ´Ø‰∏çÊîØÊåÅË∑®ÂüüCORSÊàñÊó†ÊïàÈÖçÁΩÆÂºïËµ∑ÔºåËØ∑ÂèÇËÄÉ https://ng.yunzainfo.com/docs/server Ëß£ÂÜ≥Ë∑®ÂüüÈóÆÈ¢ò',
            ev
          );
        }
        break;
    }
    if (ev instanceof HttpErrorResponse) {
      return throwError(() => ev);
    } else {
      return of(ev);
    }
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (req.context.get(ALLOW_ANONYMOUS)) return next.handle(req);
    log('yz.default.interceptor.ts: ', 'request ', req);
    // Áªü‰∏ÄÂä†ÂâçÁºÄ
    let url = req.url;
    if (!url.startsWith('https://') && !url.startsWith('http://')) {
      url = this.config.baseUrl + url;
    }
    if (url.includes('.json') && url.includes('assets')) {
      url = req.url;
    }
    // if (url.includes('i18n')) return next.handle(req);
    // Âä†ÂÖ•ËØ≠Ë®ÄÂ§¥
    const newReq = req.clone({ url, setHeaders: this.getAdditionalHeaders(req.headers) });
    return next.handle(newReq).pipe(
      mergeMap(ev => {
        // ÂÖÅËÆ∏Áªü‰∏ÄÂØπËØ∑Ê±ÇÈîôËØØÂ§ÑÁêÜ
        if (ev instanceof HttpResponseBase) {
          return this.handleData(ev, newReq, next);
        }
        // Ëã•‰∏ÄÂàáÈÉΩÊ≠£Â∏∏ÔºåÂàôÂêéÁª≠Êìç‰Ωú
        return of(ev);
      }),
      catchError((err: HttpErrorResponse) => this.handleData(err, newReq, next))
    );
  }
}

export { YunzaiDefaultInterceptor as YzDefaultInterceptor, YunzaiDefaultInterceptor };
